<template>
  <div class="main">
    <button @click="go">{{status}}</button>
    <div>
      <span>{{ intnum }}分</span>
      <span>{{decinum}}秒</span>
    </div>
    <div class="task">工作項目</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      status: "start",
      countTime: 20,
      clear: 0
    };
  },
  methods: {
    go() {
      if (this.status == "start") {
        this.startCount();
      } else {
        this.stopCount();
      }
    },
    startCount() {
      this.status = "stop";
      this.clear = setInterval(() => {
        this.countTime -= 1;
        if (this.countTime == 0) {
          this.stopCount();
        }
      }, 1000);
    },
    stopCount() {
      this.status = "start";
      clearInterval(this.clear);
    }
  },
  computed: {
    intnum() {
      return Math.floor(this.countTime / 60);
    },
    decinum() {
      return Math.floor(this.countTime % 60);
    }
  }
};
</script>

<style>
.task {
  position: relative;
  top: 25%;
  left: 25%x;
}
</style>
