<template>
  <div class="nav">
    <section class="left">
      <div>
        <MenuIcon />Tomanto Timer
      </div>
    </section>
    <section class="right">
      <span>
        {{now}}
        <MusicOff />
      </span>
    </section>
  </div>
</template>

<script>
Date.prototype.format = function(fmt) {
  //author: meizz
  var o = {
    "M+": this.getMonth() + 1, //月份
    "d+": this.getDate(), //日
    "h+": this.getHours(), //小時
    "m+": this.getMinutes(), //分
    "s+": this.getSeconds(), //秒
    "q+": Math.floor((this.getMonth() + 3) / 3), //季
    S: this.getMilliseconds() //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (this.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
};
import MenuIcon from "vue-material-design-icons/Menu.vue";
import MusicOff from "vue-material-design-icons/MusicOff.vue";

import { setInterval } from "timers";
export default {
  components: {
    MenuIcon,
    MusicOff
  },
  data() {
    return {
      now: ""
    };
  },
  mounted() {
    this.clear = setInterval(() => {
      this.now = new Date().format("yyyy MM dd hh:mm:ss  ");
    }, 1000);
  },
  methods: {}
};
</script>

